export const styles = {
  multiValue: (multiValueStyles: any) => {
    return {
      ...multiValueStyles,
      display: "none",
    };
  },
  control: (css: any) => ({
    ...css,
    width: "auto",
    border: 0,
    boxShadow: "none",
    borderRadius: 0,
    backgroundColor: "transparent",
    marginTop: -8,
  }),
  menu: ({ width, ...css }: any) => ({
    ...css,
  }),
  // Add padding to account for width of Indicators Container plus padding
  option: (css: any) => ({ ...css, paddingRight: 36 + 8 }),
};
