import React, { useCallback, useState } from "react";
import {
  InputContainer,
  Icon,
  Label,
  HintContainer,
  ErrorLabel,
  HintLabel,
} from "../TextInput/Atom.tsx";
import { Input } from "./Atom.tsx";
import { styles } from "./Styles.ts";

export interface SelectProps {
  onChange?: (value: any) => void;
  value?: any;
  style?: any;
  name?: string;
  customRef?: any;
  isFormInputElement?: boolean;
  required?: boolean;
  hint?: string;
  imageSrc?: any;
  label?: string;
  keyName?: string;
  valueName?: string;
  data: any[];
  placeholder?: string;
  errorMsg?: string;
  selectedValue?: any;
}

export interface RefProps {
  getValue: () => string;
  validate: () => any;
  name: string;
}

const Select = (props: SelectProps) => {
  const {
    onChange,
    value,
    customRef,
    name,
    required,
    hint,
    imageSrc,
    label,
    errorMsg,
    // keyName = "text",
    // valueName = "value",
    data,
    style,
  } = props;
  const getInitValue = useCallback(() => {
    return (value || "").toString();
  }, [value]);

  const [selectValue, setValue] = useState<any>(getInitValue());
  props.selectedValue && props.selectedValue(selectValue)
  const [error, setError] = useState({ error: false, message: "" });

  const validate = useCallback(
    (select: any) => {
      let newState = {
        error: false,
        message: "",
      };

      if (!select && required) {
        newState = {
          error: true,
          message: errorMsg || label + " is missing",
        };
      }
      setError(newState);
      return newState;
    },
    [label, required]
  );

  const onChangeValue = (v: any) => {
    validate(v.value);
    setValue(v.value);
    onChange && onChange(v);
    props.selectedValue && props.selectedValue(v.value)
  };

  React.useEffect(() => {
    if(customRef)
    customRef({ value: selectValue, validate, name: name || "" });
  }, [customRef, selectValue, name, validate]);

  React.useEffect(() => {
    setValue(value || "");
  }, [value]);

  const selectedModified = data?.filter(
    (option) => option.value === selectValue
  );

  return (
    <>
      <InputContainer style={style}>
        {imageSrc ? <Icon src={imageSrc} alt="" /> : null}
        {label ? (
          <Label>
            {required && <span className="required">* </span>} {label}:{" "}
          </Label>
        ) : null}
        <Input
          components={{
            DropdownIndicator: () => null,
            IndicatorSeparator: () => null,
          }}
          onChange={onChangeValue}
          value={selectedModified}
          styles={styles}
          options={data}
        />
      </InputContainer>
      <HintContainer>
        {error.error ? <ErrorLabel>{error.message}</ErrorLabel> : <div />}
        {hint ? <HintLabel>{hint}</HintLabel> : <div />}
      </HintContainer>
    </>
  );
};

export default Select;
