import styled from "styled-components";

export const InputContainer = styled.div<any>`
  border: 1px solid #00000033;
  border-radius: 4px;
  min-height: 50px;
  display: flex;
  align-items: center;
  padding: 0px 12px;
`;

export const HintContainer = styled.div<any>`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 0px;
`;

export const Input = styled.input<any>`
  height: 22px;
  text-align: left;
  letter-spacing: 0px;
  color: #000000;
  font: normal normal normal 18px/24px "Proxima Nova Rg", sans-serif;
  border: none;
  outline: none;
  border-bottom: 1px solid #00000040;
  width: 100%;
`;

export const Label = styled.label`
  text-align: left;
  font: normal normal normal 18px/24px "Proxima Nova Rg", sans-serif;
  letter-spacing: 0px;
  color: #00000099;
  margin-right: 12px;
  flex: 0 0 auto;
`;

export const Icon = styled.img`
  width: 36px;
  height: 27px;
  object-fit: contain;
  margin-right: 12px;
`;

export const ErrorLabel = styled.label`
  text-align: left;
  font: normal normal normal 12px/24px "Proxima Nova Rg", sans-serif;
  letter-spacing: 0px;
  color: #ff767c;
`;

export const HintLabel = styled.label`
  text-align: left;
  font: normal normal normal 12px/24px "Proxima Nova Rg", sans-serif;
  letter-spacing: 0px;
  color: #00000066;
`;
