import "./index.css";
import Axios from "../../../api/Axios";
import { DragDropContext, Draggable, Droppable } from "react-beautiful-dnd";

// import userdata from "./tempData.json";
import { useCallback, useEffect, useState } from "react";
import Loader from "../../../components/Loader";
import { toast } from "react-toastify";
import { isEqual } from 'lodash';
import { useNavigate } from "react-router-dom";
import edit from "../../../assets/edit.svg";
import Select from "../../../components/Select/index.tsx";
import triggerSwalAlert from "../../../helpers/triggerSwalAlert";

const axios = new Axios();

const ManageProducts = () => {
  const [products, setProducts] = useState([])
  const [productsBackup, setProductsBackup] = useState([])
  const [pending, setPending] = useState(false);
  const [isSaveOrderBtnDisabled ,setIsSaveOrderBtnDisabled] = useState(true)
  const [chooseProductCategory, setChooseProductCategory] = useState([]);
  const navigate = useNavigate();

  const handleDragEnd = (e) => {
    if (!e.destination) return;
    let tempProductsArray = JSON.parse(JSON.stringify(productsBackup));
    let [source_data] = tempProductsArray.splice(e.source.index, 1);
    tempProductsArray.splice(e.destination.index, 0, source_data);
    if(!isEqual(tempProductsArray, products)){
      setIsSaveOrderBtnDisabled(false)
    }
    else{
      setIsSaveOrderBtnDisabled(true)
    }
    setProductsBackup(tempProductsArray);
  };

  const getAllProducts = useCallback(async () => {
    setPending(true)
    let options = {
      method: "GET"
    }
    let result = await axios.callApi("/product/getAllProducts", options);
    if (result?.status == 200) {
      setPending(false)
      setProducts(result?.data?.data)
      setProductsBackup(result?.data?.data)
    }
    else {
      setPending(false)
      toast.error("Something went wrong while fetching products")
    }
  }, [])

  const getAllProductCategories = useCallback(async () => {
    let options = {
      method: "GET"
    }
    let result = await axios.callApi("/addProduct/chooseHierarchyInAddProduct", options);
    if (result?.status == 200) {
      setChooseProductCategory(result?.data?.data)
    }
    else {
      toast.error("Something went wrong while fetching categories")
    }
  }, [])

  const handleSwitchChange = async (value, productId, productAttributeId, columnName)=> {
      setPending(true)
      let flag = value.target.checked
      let options = {
        method: "POST",
        body: {
          productId,
          productAttributeId,
          columnName,
          flag
        }
      }
      let result = await axios.callApi("/product/editProductAttributeColumns", options)
      if(result?.status == 200){
        setPending(false)
        toast.success("Updated Successfully")
      }
      else{
        setPending(false)
        toast.error("Something went wrong")
      }
  }

  const handleSaveProductsOrder = async () => {
      setPending(true)
      let payload = {}
      let orderIndex = 1
      productsBackup.forEach((product)=> {
         if(payload[product.id]){
          payload[product.id][product.productDetails.id] = orderIndex
         }
         else{
          payload[product.id] = {}
          payload[product.id][product.productDetails.id] = orderIndex
         }
         orderIndex += 1
      })
      let options = {
        method: "POST",
        body: {
          payload
        }
      }
      let result = await axios.callApi("/product/saveOrderOfProducts", options)
      if(result?.status == 200){
        setPending(false)
        triggerSwalAlert("Success", "Updated Successfully", "success")
      }
      else{
        setPending(false)
        triggerSwalAlert("Error", "Something went wrong", "error")
      }
  }

  useEffect(() => {
    getAllProducts()
    getAllProductCategories()
  }, [])

  const onChangeCategory = async (value, product)=>{
    setPending(true)
    let options = {
      method: "POST",
      body: {
        productId: product.id,
        hierarchyId: value.value
      }
    }
    let result = await axios.callApi("/product/updateProductHierarchy", options)
    if(result?.status == 200){
      setPending(false)
      toast.success("Updated Successfully")
    }
    else{
      setPending(false)
      toast.error("Something went wrong")
    }
  }

  const onChangeGender = async (value, product)=>{
    setPending(true)
    let options = {
      method: "POST",
      body: {
        productId: product.id,
        gender: value.value
      }
    }
    let result = await axios.callApi("/product/updateProductGender", options)
    if(result?.status == 200){
      setPending(false)
      toast.success("Updated Successfully")
    }
    else{
      setPending(false)
      toast.error("Something went wrong")
    }
  }

  return (
    <>
    {pending ? <Loader /> : null}
    <div className="ManageProductsMainClass mt-4">
      <div className="ManageproductsHeader">
        <button onClick={() => navigate(`/add-product`)} style={{float: "left"}}>+ Add Product</button>
        <button disabled={isSaveOrderBtnDisabled} className={isSaveOrderBtnDisabled ? "disabled": ""} style={{float: "right"}} onClick={handleSaveProductsOrder} >Save Products Order</button>
      </div>
      <DragDropContext onDragEnd={handleDragEnd}>
        <table className="table borderd">
          <thead>
            <tr>
              <th />
              <th>Sr. No.</th>
              <th>Perfume</th>
              <th>Attribute</th>
              <th>Category</th>
              <th>Gender</th>
              <th>Product Status</th>
              <th>Best Seller Status</th>
              <th>New</th>
              <th>Edit</th>
            </tr>
          </thead>
          <Droppable droppableId="droppable-1">
            {(provider) => (
              <tbody
                className="text-capitalize"
                ref={provider.innerRef}
                {...provider.droppableProps}
              >
                {productsBackup?.map((product, index) => (
                  <Draggable
                    key={product.productDetails.id}
                    draggableId={product.productDetails.id}
                    index={index}
                  >
                    {(provider) => (
                      <tr {...provider.draggableProps} ref={provider.innerRef}>
                        <td {...provider.dragHandleProps}> = </td>
                        <td>{index + 1}</td>
                        <td>{product.productName}</td>
                        <td>{Object.values(product.productDetails.attributeCombination)}</td>
                        <td style={{width: "250px"}}>
                          <Select
                            name="category"
                            value={product?.["hierarchyId"]}
                            data={chooseProductCategory}
                            onChange={(value)=>onChangeCategory(value,product)}
                          />
                        </td>
                        <td style={{width: "250px"}}>            
                          <Select
                            name="gender"
                            value={product?.["gender"]}
                            data={[{
                              label: "Men",
                              value: "Men"
                            },{
                              label: "Women",
                              value: "Women"
                            },{
                              label: "Unisex",
                              value: "Unisex"
                            }]}
                            onChange={(value)=>onChangeGender(value,product)}
                        />
                        </td>
                        <td>
                          <div className="form-check form-switch">
                            <input className="form-check-input" type="checkbox" role="switch" id="flexSwitchCheckChecked" defaultChecked={product.productDetails.isActive} onChange={(value)=>handleSwitchChange(value, product.id, product.productDetails.id, "isActive")}></input>
                          </div>
                        </td>
                        <td>
                          <div className="form-check form-switch">
                            <input className="form-check-input" type="checkbox" role="switch" id="flexSwitchCheckChecked" defaultChecked={product.productDetails.bestSellerStatus} onChange={(value)=>handleSwitchChange(value, product.id, product.productDetails.id, "bestSellerStatus")}></input>
                          </div>
                        </td>
                        <td>
                          <div className="form-check form-switch">
                            <input className="form-check-input" type="checkbox" role="switch" id="flexSwitchCheckChecked" defaultChecked={product.productDetails.isNew} onChange={(value)=>handleSwitchChange(value, product.id, product.productDetails.id, "isNew")}></input>
                          </div>
                        </td>
                        <td>
                          <div onClick={() => navigate(`/edit-product/${product.id}`)}>
                            <img src={edit} alt="" />
                          </div>
                        </td>
                      </tr>
                    )}
                  </Draggable>
                ))}
                {provider.placeholder}
              </tbody>
            )}
          </Droppable>
        </table>
      </DragDropContext>
    </div>
    </>
  );
}

export default ManageProducts;
